export default {
  name: "Main",
  data() {
    return {
      navList: [
        {
          name: "首页",
          path: "/index",
          class: "footer_img_index",
          type: true
        },
        {
          name: "课程",
          path: "/curriculum",
          class: "footer_img_curriculum",
          type: false
        },
        {
          name: "发现",
          path: "/discover",
          class: "footer_img_discover",
          type: false
        },
        {
          name: "任务",
          path: "/task",
          class: "footer_img_task",
          type: false
        },
        {
          name: "我的",
          path: "/mine",
          class: "footer_img_me",
          type: false
        }
      ]
    };
  },
  computed: {},
  mounted() {
    this.setNavColor();
  },
  methods: {
    clickAppNav: function(item, index) {
      const _this = this;
      this.navList.map(function(event, idx) {
        if (event.name == item.name) {
          event.type = true;
          _this.$set(_this.navList, idx, event);
          // this.$router.push({ path: event.path});
        } else {
          event.type = false;
          _this.$set(_this.navList, idx, event);
        }
      });
    },
    setNavColor: function() {
      const _this = this;
      let urlRef = window.location.href;
      let urlArr = urlRef.split("/");
      this.navList.map(function(ev, idx) {
        urlArr.map(function(event, index) {
          let loacationUrl = "/" + event;
          let dataUrl = ev.path;
          if (loacationUrl == dataUrl) {
            ev.type = true;
            _this.$set(_this.navList, idx, ev);
          } else {
            ev.type = false;
            _this.$set(_this.navList, idx, ev);
          }
        });
      });
    }
  }
};



